<template>
  <div class="flex-column-start main">
    <NavBar :group-list="groupList" @nav-click="goPath" />
    <div class="flex-h__1 flex-row-start">
      <Menu :group-list="groupList" class="menu" @nav-click="goPath" />
      <div class="flex-1 main-content">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '@/componets/dappNav.vue'
import Menu from '@/componets/Menu.vue'
import { routes } from '@/router/index.js'
export default {
  components: {
    NavBar,
    Menu
  },
  data() {
    return {
      groupList: []
    }
  },
  created() {
    const dapp = routes.find(item => {
      return item.name === 'dapp'
    })
    const list = dapp.children.filter(item => {
      if (item.icon) {
        return item
      }
    })
    const res = {}
    list.forEach(item => {
      const arr = item.path.split('/')
      res[arr[2]] = res[arr[2]] || []
      res[arr[2]].push(item)
    })
    this.groupList = res
  },
  methods: {
    goPath(name) {
      console.log(name)
      this.$router.push({
        name
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@media only screen and (max-width: 992px) {
  .menu {
    display: none !important;
  }
}
.main {
  background: #101529;
  height: 100%;
  overflow: hidden;
  .main-content {
    height: 100%;
    overflow: auto;
  }
}
</style>
