<template>
  <div class="menu text-left">
    <div v-for="(item, key) in groupList" :key="key" class="menu-group">
      <div class="menu-group__title text-16 MB-20">
        {{ key }}
      </div>
      <div
        v-for="(child, i) in item"
        :key="i"
        class="menu-item flex-row-start text-16 text-bold align-center pointer"
        :class="{ active: child.name == $route.name }"
        @click="goPath(child.name)"
      >
        <img v-if="child.name == $route.name" :src="child.activeIcon" class="menu-item__icon">
        <img v-else :src="child.icon" class="menu-item__icon">
        <span>{{ child.title }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    groupList: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    goPath(name) {
      this.$emit('navClick', name)
    }
  }
}
</script>

