<template>
  <nav class="nav flex-row-between PR-40">
    <a href="" class="nav-logo flex-row-center">
      <img src="~@/assets/images/common/logo.png" class="nav-logo__pic">
    </a>
    <div class="nav-content flex-1 flex-row-end">
      <div class="nav-search flex-1 flex-row-start align-center PL-30 PR-30">
        <el-icon color="#fff" :size="18" class="MR-30">
          <search />
        </el-icon>
        <input type="text" class="flex-1 text-16 nav-search__input" placeholder="Search market or enter a token access...">
      </div>
      <div class="nav-btn ML-30 text-white flex-row-start align-center pointer">
        <span class="MR-5">Dapp</span>
        <el-icon color="#fff" size=".2rem"><refresh /></el-icon>
      </div>
      <div class="nav-btn ML-30 text-white flex-row-start align-center pointer">
        <img src="~@/assets/images/common/money1.png" class="nav-btn__money">
        <span class="ML-10 color-text">${{ filterPoint(utils.formatEther(balance)) }}</span>
      </div>
      <div class="nav-btn ML-30 setting text-white flex-row-center align-center pointer">
        <img src="~@/assets/images/common/setting@2x.png" class="nav-btn__money">
      </div>
      <div class="nav-connect pointer ML-40">
        <div class="flex-row-center nav-connect__content">
          <el-dropdown v-if="getAccount">
            <div class="el-dropdown-link color-text flex-row-center">
              <span>{{ getAccount }}</span>
            </div>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="logout">Logout</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
          <div v-else class="flex-row-center" @click="connectHandler">
            <img v-if="!connectLoading" src="~@/assets/images/common/connect.png" class="nav-connect__icon MR-10">
            <el-icon v-else class="is-loading MR-10" color="#fff" :size="20">
              <loading />
            </el-icon>
            <span class="color-text text-16">Connected</span>
          </div>
        </div>
      </div>
      <el-icon class="ML-40 pointer nav-menu" :size="20" color="#fff" @click="drawer = true"><Menu /></el-icon>
    </div>

    <el-drawer v-model="drawer" :show-close="false" custom-class="nav-drawer" :with-header="false">
      <div class="nav-search flex-1 flex-row-start align-center PL-20 PR-20">
        <el-icon color="#fff" :size="20" class="MR-10">
          <search />
        </el-icon>
        <input type="text" class="flex-1 text-16 nav-search__input" placeholder="Search market or enter a token access...">
      </div>
      <div class="flex-row-start MT-20 flex-wrap">
        <div class="nav-btn text-white flex-row-start align-center pointer MR-10 MB-10">
          <span class="MR-5">Dapp</span>
          <el-icon color="#fff" size=".2rem"><refresh /></el-icon>
        </div>
        <div class="nav-btn text-white flex-row-start align-center pointer MR-10 MB-10">
          <img src="~@/assets/images/common/money1.png" class="nav-btn__money">
          <span class="ML-10 color-text">${{ filterPoint(utils.formatEther(balance)) }}</span>
        </div>
        <div class="nav-btn setting text-white flex-row-center align-center pointer">
          <img src="~@/assets/images/common/setting@2x.png" class="nav-btn__money">
        </div>
      </div>
      <Meun :group-list="groupList" @nav-click="goPath" />
    </el-drawer>
  </nav>
</template>

<script>
import { Search, Refresh, Menu, Loading } from '@element-plus/icons'
import { ElNotification } from 'element-plus'
import Meun from './Menu.vue'
import { BigNumber, utils } from 'ethers'
export default {
  components: {
    Search,
    Refresh,
    Menu,
    Meun,
    Loading
  },
  props: {
    groupList: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      utils,
      account: '',
      connectLoading: false,
      drawer: false,
      balance: BigNumber.from(0)
    }
  },
  computed: {
    getAccount() {
      if (this.account) {
        return '0x....' + this.account.substr(-4)
      }
      return ''
    }
  },
  mounted() {
    this.$wallet.getUSDT().then(usdt => {
      console.log(usdt)
      this.balance = usdt
    })
    this.account = this.$wallet.accounts[0]
    this.$wallet.on('accountChange', async() => {
      this.account = this.$wallet.accounts[0]
    })
  },
  methods: {
    filterPoint(str) {
      console.log(str)
      const index = str.indexOf('.')
      return str.substr(0, index + 9)
    },
    async getBalance() {
      await this.$wallet.initNetwork()
      this.balance = await this.$wallet.scoutDogeABI.balanceOf(this.$wallet.accounts[0])
    },
    goPath(name) {
      this.drawer = false
      this.$emit('navClick', name)
    },
    logout() {
      this.$wallet.logout()
    },
    async connectHandler() {
      if (this.connectLoading) {
        return
      }
      this.connectLoading = true
      await this.$wallet.initNetwork()
      this.connectLoading = false
    },
    /**
     * 跳转到指定页面
     * @param {String} path 页面路由地址
     */
    togglePage(path) {
      if (path === this.$route.path) {
        this.$router.push({
          path: '/redirect',
          query: path
        })
        return
      }
      ElNotification({
        title: 'Coming soon',
        message: 'Coming soon'
      })
      return
      // if (!path) {
      //   return
      // }
      // this.$router.push({
      //   path
      // })
    }
  }
}
</script>

